.timeline-item 
  display: flex 
  @extend %position-relative 
  @extend %z-index-p 
  padding: rem(15px) 0 
  &:last-child
    &::before 
      display: none 
  &::before 
    @extend %position-absolute 
    content: '' 
    top: 38px
    left: 9px
    width: 2px 
    height: calc(100% - 16px) 
    background-color: var(--border-color) 
    @extend %z-index-c
  .timeline-circle 
    width: 20px 
    height: 20px 
    border-width: 2px 
    border-style: solid 
    @extend %bs-50 
    margin-top: rem(5px)  
  .timeline-content 
    padding-left: rem(20px) 
    width: calc(100% - 20px) 