/* auth page css start */
.auth-page 
  min-height: 100vh 
  display: flex
  flex-flow: column
  @extend %justify-center 
.auth-logo 
  img 
    max-height: 35px 

.auth-card 
  padding: rem(40px) rem(30px)
  background-color: var(--card-bg) 
  @extend %bs-8 
  border: 1px solid var(--border-color)
/* auth page css end */