/* button css start */
.btn 
  @extend %bs-5 
  i[class*="la-"]
    font-size: rem(20px)
  &.icon-prepend 
    i 
      padding: rem(8px) rem(10px) rem(10px) 0 
      margin: rem(-7px) rem(8px) rem(-7px) 0
      border-right: 1px solid #fff 
  &.icon-append 
    i 
      padding: rem(8px) rem(10px) rem(10px)
      margin: rem(-7px) rem(-10px) rem(-7px) rem(8px)
      border-left: 1px solid #fff 
  &.btn-c-icon 
    @extend %d-inline-flex 
    @extend %align-center 
    i 
      width: 30px
      height: 30px
      margin-inline-end: rem(8px)
      @extend %bg-white 
      @extend %text-h 
      @extend %d-flex 
      @extend %align-center
      @extend %justify-center
      @extend %bs-50 

.toggle-group
  .btn 
    font-size: rem(14px)
    border: none
    &:not(.toggle-handle)
      padding: rem(5px) rem(15px)
    &.btn-success
      background-color: var(--color-success)
    &.btn-danger
      background-color: var(--color-danger)
  

.btn-default
  color: #333
  background-color: #fff
  border-color: #ccc

.s7__btn-primary
  background-color: var(--color-primary)
  @extend %text-white 
  &:hover 
