.pricing-item 
  padding: rem(25px)
  background-color: var(--card-bg)
  border: 1px solid var(--border-color) 
  @extend %bs-8 
  .price 
    font-weight: 600  
    sub 
      font-weight: 400 
      font-size: 50%

.pricing-features-list 
  list-style: none 
  padding-inline-start: 0 
  margin-bottom: 0 
  li + li 
    margin-top: rem(10px)
  li 
    @extend %position-relative 
    padding-inline-start: 26px
    &::before 
      @extend %position-absolute 
      content: "\f058"
      font-family: "Font Awesome 5 Free"
      font-weight: 900 
      left: 0
      top: 0 
      color: lighten(#000, 55%) 

.pricing-item-two 
  padding: rem(25px) 
  @extend %bs-8 
  background-color: var(--card-bg)