.exchange-radio-field 
  padding: rem(5px)
  @extend %bs-8 
  background-color: var(--body-bg) 
  @extend %d-flex 
  .exchange-radio-option 
    width: 50% 
    @extend %position-relative 
    input 
      @extend %position-absolute 
      top: 0 
      left: 0 
      opacity: 0 
      &:checked ~ label 
        background-color: var(--card-bg) 
        box-shadow: 0 0 2px rgba(#000, 0.1)
    label 
      @extend %d-flex 
      @extend %align-center 
      font-weight: 600 
      @extend %justify-center 
      margin-bottom: 0 
      padding: rem(8px) rem(15px) 
      @extend %bs-5 
      cursor: pointer 
      @extend %transition 
      i 
        margin-inline-end: rem(5px) 
        &[class*="las"]
          font-size: rem(20px)

.exchange-input-flied 
  background-color: var(--body-bg)
  padding: rem(5px) 
  @extend %bs-10  
  @extend %d-flex 
  @extend %align-center 
  justify-content: space-between
  select 
    background-color: var(--card-bg)
    height: 36px 
    +border-radius(999px) 
    padding: rem(5px) rem(15px)
    font-size: rem(14px)
    border-color: transparent 
    color: var(--para-color)
    &:focus 
      outline: none 
      border: 1px solid var (--color-primary) 
  input 
    border: none 
    background-color: transparent 
    text-align: right
    padding-inline-end: rem(15px)
    width: 150px
    color: var(--h-color) 
    &:focus 
      outline: none 
      border: none 

.border-ring 
  border: 2px solid var(--color-primary)
  width: 15px 
  height: 15px 
  @extend %bs-50 

.theme-dark 
  .apexcharts-canvas
    .apexcharts-yaxis,
    .apexcharts-xaxis
      text
        fill: var(--h-color)
    .apexcharts-title-text
      fill: #fff !important 
  