/* card css start */
.card 
  @extend %card-bg
  border-color: var(--border-color)
  box-shadow: 0 1px 2px #38414a26
  @extend %bs-8
  .card-body 
    padding: rem(20px)
    color: var(--para-color) 
.card-header 
  @extend %card-bg
  padding: rem(15px) rem(20px)
  border-color: var(--border-color)
  +border-radius(8px 8px 0 0 !important)
  .card-title 
    font-size: rem(18px)
    margin-bottom: 0

.card 
  --body-bg : #e7ecf1 
  border-color: transparent 

.chart-bottom-minus 
  margin-bottom: -15px 

.theme-dark 
  .card
    box-shadow: 0 5px 25px rgba(#000, 0.2)
    --body-bg: #18202f
    border-color: var(--border-color)
/* card css end */