.profile-pic-area 
  display: inline-block
  @extend %position-relative 
  .profile-pic
    width: 125px
    height: 125px
    display: inline-block
    @extend %obj-fit
  .file-upload
    display: none
  .circle
    border-radius: 100% !important
    overflow: hidden
    width: 128px
    height: 128px
    border: 2px solid rgba(255, 255, 255, 0.2)
  img
    max-width: 100%
    height: auto
  .p-image
    position: absolute
    bottom: 8px
    right: 17px
    color: #666666
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)
    &:hover
      transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)
  .upload-button
    font-size: 1.2em
    &:hover
      transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)
      color: #999
  &.style-two 
    display: block
    .circle 
      width: 100%
      height: 350px 
      +border-radius(10px !important)
      border: 2px solid var(--border-color) 
      img 
        width: 100% 
        height: 100% 
        @extend %obj-fit 
        display: block
    .p-image 
      position: static 
    .upload-button 
      &:hover 
        color: #fff 