.theme-dark 
  label 
    color: #e5e5e5
  
label
  font-weight: 400
  margin-bottom: 6px
  color: lighten(#495057, 10%)
  font-size: rem(12px) 
.input-group-text
  margin-bottom: 0
  background-color: var(--body-bg)
  border-color: var(--border-color)
  color: var(--h-color)
.form-control
  background-color: var(--card-bg)
  height: 42px
  border-color: var(--border-color)
  color: var(--h-color)
  font-size: rem(15px)
  &:focus
    background-color: var(--card-bg)
    border-color: var(--color-primary)
    box-shadow: 0 2px 5px rgba(var(--color-primary), 0.1)
    color: var(--h-color)
  &[type="file"]
    line-height: 28px
  &.form-control-sm[type="file"]
    line-height: 32px
  &.form-control-lg[type="file"]
    line-height: 31px
  &:disabled, &[readonly]
    background-color: var(--body-bg)

textarea
  &.form-control
    height: auto
  &.resize-off
    resize: none

.form-select, .select
  height: 42px
  border-color: var(--border-color)
  background-color: var(--card-bg)
  color: var(--h-color)
  font-size: rem(15px)
  &.form-select-lg
    height: 50px
  &.form-select-sm
    height: 36px
  &:focus
    border-color: var(--color-primary)
    box-shadow: 0 2px 5px rgba(var(--color-primary), 0.1)
.theme-dark
  .form-select
    background-image: url(../images/icons/down-arrow.png)

.input-group
  &.input-group-sm
    .form-control
      height: 36px
  &.input-group-lg
    .form-control
      height: 50px

.form-check-input-lg 
  width: 50px !important
  height: 22px !important 

.form-check-input
  &:not(:checked)
    background-color: #fff !important
    border-color: var(--border-color) !important

.flex-grow-12 
  flex-grow: 12 !important


.onOff-radio-field 
  padding: rem(6px)
  background-color: var(--body-bg) 
  @extend %d-flex 
  border: 2px solid var(--color-primary)
  +border-radius(99px)
  width: 150px
  .onOff-radio-option 
    width: 50% 
    @extend %position-relative 
    input 
      @extend %position-absolute 
      top: 0 
      left: 0 
      opacity: 0 
      &:checked ~ label 
        background-color: var(--color-primary-light) 
        color: #fff
        box-shadow: 0 0 2px rgba(#000, 0.1)
    label 
      @extend %d-flex 
      @extend %align-center 
      font-weight: 600 
      @extend %justify-center 
      margin-bottom: 0 
      padding: rem(6px) rem(15px) 
      +border-radius(99px)
      cursor: pointer 
      @extend %transition 
      i 
        margin-inline-end: rem(5px) 
        &[class*="las"]
          font-size: rem(20px)