%position-relative
  position: relative
%position-absolute
  position: absolute
%position
  +position
%positionTwo
  +positionTwo

%z-index-p
  z-index: 1
%z-index-c
  z-index: -1
  
%transition
  -webkit-transition: all 0.3s
  -o-transition: all 0.3s
  transition: all 0.3s

%base-color
  background-color: var(--color-primary)
%base-color-two
  background-color: $base-color-two
%bg-white
  background-color: #fff  

%card-bg 
  background-color: var(--card-bg)

%text-base 
  color: var(--color-primary)
%text-white
  color: #fff
%text-h
  color: $heading-color 
%text-p
  color: $para-color 

%text-d-none
  text-decoration: none

%list-reset
  padding-inline-start: 0
  list-style: none
  margin-bottom: 0

%obj-fit
  +object-fit

%d-flex 
  +d-flex 
%justify-center
  justify-content: center
%align-center
  align-items: center
%d-block
  display: block
%d-inline-block 
  display: inline-block
%d-inline-flex
  display: inline-flex
%overflow-hidden 
  overflow: hidden

%w-100
  width: 100%
%h-100
  height: 100%

%trans-y
  +transform(translateY(-5px))
%text-center 
  text-align: center

%bs-5 
  +border-radius(5px)
%bs-8
  +border-radius(8px)
%bs-10
  +border-radius(10px)
%bs-50
  +border-radius(50%)

%shadow-1 
  // box-shadow: 0 5px 20px rgba(#000, 0.03)
  box-shadow: 0 1px 2px #38414a26

%shadow-2
  box-shadow: 0 5px 20px rgba(#000, 0.03)