/* reset css start */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap')

$heading-font: 'Inter', sans-serif
$para-font: 'Inter', sans-serif

*
  margin: 0
  padding: 0

$h1: rem(48px)
$h1-1: rem(36px)
$h1-2: rem(32px)

$h2: rem(40px)
$h2-1: rem(32px)
$h2-2: rem(24px)

$h3: rem(32px)
$h3-1: rem(24px)
$h3-2: rem(20px)

$h4: rem(24px)
$h4-1: rem(18px)
$h4-2: rem(16px)

$h5: rem(20px)
$h5-1: rem(18px)
$h5-2: rem(16px)

$h6: rem(16px)


:root 
  /* main color variable */
  // --color-primary: #6366f1
  --color-primary: #2196f3
  --color-primary-light: #5eb1f5
  --color-primary-lighten: #c2e4ff 

  --color-secondary: #868e96
  --color-secondary-light: #949ba2
  --color-secondary-lighten: #d8dbdd 

  --color-success: #28c76f
  --color-success-light: #33d67c
  --color-success-lighten: #c6ffe0 

  --color-danger: #ea5455
  --color-danger-light: #ed6b6c
  --color-danger-lighten: #ffdfdf 

  --color-warning: #ff9f43
  --color-warning-light: #ffac5d
  --color-warning-lighten: #ffefdf 

  --color-info: #1e9ff2 
  --color-info-light: #36a9f3 
  --color-info-lighten: #d5efff 

  --color-dark: #10163A 
  --color-dark-light: #161e4e 
  --color-dark-lighten: #d6dcff 

  --color-light: #bcc7da 
  --color-muted: #74788d 

  /* site custom bg variable */
  --color-1: #764AF1
  --color-2: #10b7cd
  --color-3: #005555
  --color-4: #4D4C7D
  --color-5: #143F6B
  --color-6: #FD5D5D
  --color-7: #019267
  --color-8: #5463FF
  --color-9: #FF5C8D
  --color-10: #FF5C8D

  /* font weight config variable */
  --f-light: 300
  --f-regular: 400
  --f-medium: 500
  --f-semibold: 600
  --f-bold: 700

  /* font size and line-height variable */
  --h1: #{$h1} /* 48px */
  --h1-lg: #{$h1-1} /* 36px */
  --h1-sm: #{$h1-2} /* 32px */

  --h2: #{$h2} /* 40px */
  --h2-lg: #{$h2-1} /* 32px */
  --h2-sm: #{$h2-2} /* 24px */

  --h3: #{$h3} /* 32px */
  --h3-lg: #{$h3-1} /* 24px */
  --h3-sm: #{$h3-2} /* 20px */

  --h4: #{$h4} /* 24px */
  --h4-lg: #{$h4-1} /* 18px */
  --h4-sm: #{$h4-2} /* 16px */

  --h5: #{$h5} /* 20px */
  --h5-lg: #{$h5-1} /* 18px */
  --h5-sm: #{$h5-2} /* 16px */

  --h6: #{$h6} /* 16px */

  --h1-lineHeight: 1.25
  --h2-lineHeight: 1.24
  --h3-lineHeight: 1.4
  --h4-lineHeight: 1.38
  --h5-lineHeight: 1.5
  --h6-lineHeight: 1.6

html
  scroll-behavior: smooth

.theme-light
  /* light version site bg color variable */
  --body-bg: #F1F5F9
  --card-bg: #fff

  /* light version site config color variable */
  --h-color: #334155
  --para-color: #64748b
  --border-color: #d6d6d6

.theme-dark
  // --body-bg: #1B1C31
  // --card-bg: #24243C

  /* dark version site bg color variable */
  --body-bg: #18202f
  --card-bg: #1e293b

  /* dark version site config color variable */
  --h-color: #fff
  --para-color: #fff
  --border-color: rgba(255,255,255, 0.15)


body
  font-family: $para-font
  color: var(--para-color)
  font-size: rem(15px)
  font-weight: 400
  line-height: 1.7
  // background-color: var(--body-bg)
  background-color: #F1F5F9
  @extend %position-relative
  &::before
    @extend %position-absolute
    content: ''
    top: 0
    left: 0
    width: 0
    @extend %h-100
    background-color: #000
    opacity: 0.35
    z-index: 3
    @extend %transition
  &.dark-overlay
    &::before
      @extend %w-100

.theme-dark
  body
    background-color: $base-color-two

img
  max-width: 100%

a
  text-decoration: none
  color: var(--para-color) 
  &:hover 
    color: var(--color-primary)
/* reset css end */
