/* table css start */
[dir="rtl"]
  .s7__table
    tbody
      td 
        &:first-child
          text-align: right 

.s7__table
  margin-bottom: 0
  color: var(--h-color)
  &.border-0 
    td 
      padding-block: rem(7px) 
      border: none  
  &.px-0 
    td 
      padding-left: 0 !important
      padding-right: 0 !important
  thead
    +media(991px)
      display: none
    th
      // @extend %text-center
      padding: rem(8px) rem(15px)
      font-weight: 600
      font-size: rem(13px)
      background-color: #eaedf0
      color: var(--h-color)
      border-color: var(--border-color)
      border-top: 1px solid var(--border-color)
      text-transform: uppercase
      // &:first-child
      //   text-align: left
      &:last-child
        text-align: right
  tbody
    border-top: none !important
    tr
      &:last-child
        td
          border-bottom: none
          +media(991px)
            border-bottom: 1px solid var(--border-color)
      &:nth-child(even)
        +media(991px)
          background-color: var(--body-bg) 
    td
      // @extend %text-center
      padding: rem(15px) rem(15px)
      vertical-align: middle
      border-color: var(--border-color)
      +media(991px)
        text-align: right
      +media(991px)
        display: block
      &:first-child
        text-align: left
        +media(991px)
          text-align: right
      &:last-child
        text-align: right
        +media(991px)
          text-align: right


.page-item
  .page-link
    color: var(--para-color)
    background-color: var(--card-bg)
    border-color: var(--border-color) 
    &:focus
      box-shadow: none
  &.active 
    .page-link 
      background-color: var(--color-primary)
      border-color: var(--color-primary)
      color: #fff 


.theme-dark
  .s7__table
    thead
      th
        background-color: var(--body-bg)

.table-icon
  padding: 0
  border: none
  background-color: transparent
  i
    &[class*="la-"]
      font-size: rem(24px)
      line-height: 1

[data-caption]
  @extend %position-relative
  +media(991px)
    padding-inline-start: 40% !important
  &::before
    @extend %position-absolute
    content: attr(data-caption)
    left: 10px
    top: 50%
    +transform(translateY(-50%))
    opacity: 0
    visibility: hidden
    +media(991px)
      opacity: 1
      visibility: visible

.table>:not(caption)>*>* 
  border-color: var(--border-color) 
  color: var(--para-color)

.table-striped>tbody>tr:nth-of-type(odd)>* 
  color: var(--para-color)

/* table css end */ 
