.profile-pic-wrapper 
  @extend %d-flex 
  @extend %align-center 
  .profile-thumb-box
    width: 100px
    height: 100px
    @extend %bs-5 
    @extend %overflow-hidden 
    box-shadow: 0 5px 10px rgba(#000, 0.1)
    img 
      @extend %w-100 
      @extend %h-100 
      @extend %obj-fit 
  .upload-button 
    font-size: rem(14px) 
  .profile-up-btn 
    padding-inline-start: rem(25px)
