/* image uploader css start */

.wrap
  border-radius: 4px
  background-color: var(--body-bg)
  box-shadow: 0 1px 2px 0 rgba(#000, 0.15)
  padding: rem(20px)

// ------------  File upload BEGIN ------------ 
.files
  position: relative
  font-size: rem(17px)
  font-weight: 600
  &__input
    background-color: var(--card-bg)
    border-radius: 3px
    color: var(--para-color)
    padding: rem(15px) rem(17px)
    &--file
      position: absolute
      opacity: 0
    &--label
      display: flex
      justify-content: space-between
      align-items: center
      margin-bottom: 0
      cursor: pointer
      &:after
        content: attr(data-text-btn)
        border-radius: 3px
        @extend %base-color
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.18)
        padding: rem(15px) rem(17px)
        margin: rem(-15px) rem(-17px)
        color: rgba(255, 255, 255, 1)
        cursor: pointer
  &__value
    padding: rem(10px)
    @extend %bs-5
    background-color: var(--card-bg)
    @extend %d-flex
    justify-content: space-between
    @extend %align-center
    @extend %w-100
    color: var(--para-color)
    margin-top: rem(15px)
    @extend %position-relative
    &:hover
      &:after
        @extend %text-white
    &:after
      @extend %position-absolute
      content: "\f00d"
      top: 0
      right: 0
      width: 40px
      @extend %h-100
      background-color: $danger
      @extend %text-white
      font-size: rem(18px)
      @extend %d-flex
      @extend %align-center
      @extend %justify-center
      font-family: 'Line Awesome Free'
      font-weight: 900
      cursor: pointer
      @extend %bs-5
  &__remove
    display: block
    width: 20px
    height: 20px
    border: 1px solid #000

#files_img
  display: block

.single-upload-img
  display: inline-block
  margin: 10px 10px 0 0
  background-color: var(--card-bg)
  @extend %bs-5
  @extend %overflow-hidden

.imageThumb
  max-height: 75px
  cursor: pointer

.single-upload-remove
  display: block
  background: $danger
  @extend %text-white
  text-align: center
  cursor: pointer
  margin-top: -1px
  padding: rem(3px) rem(5px)

/* image uploader css end */
