/* nav-tabs css start */
.s7__line-tabs
  border-color: var(--border-color)
  .nav-item
    .nav-link
      background-color: transparent
      border: none
      font-weight: 500
      color: var(--para-color)
      @extend %position-relative
      @extend %d-flex
      @extend %align-center
      &::after
        @extend %position-absolute
        content: ''
        bottom: 0
        left: 0
        @extend %w-100
        height: 2px
        @extend %base-color
        opacity: 0
        @extend %transition
      i
        font-size: rem(18px)
        margin-inline-end: rem(5px)
      &.active
        @extend %text-base
        &::after
          opacity: 1
      &.answered
        &.active
          color: $success
          &::after
            background-color: $success
      &.delete
        &.active
          color: $danger
          &::after
            background-color: $danger

.s7_pills-tab 
  .nav-item 
    .nav-link 
      color: var(--para-color) 
      &.active 
        background-color: var(--color-primary)
        color: #fff 

.s7_verticle-nav 
  .nav-item 
    .nav-link 
      color: var(--para-color)
      &.active 
        background-color: var(--color-primary)
        color: #fff 
/* nav-tabs css end */
