/* The switch - the box around the slider

.switch
  @extend %position-relative
  display: inline-block
  width: 30px
  height: 30px
  margin-bottom: 0
  input
    opacity: 0
    width: 0
    height: 0
    &:checked ~ .switch-icons 
      .feather-sun 
        opacity: 0
      .feather-moon 
        opacity: 1
      
  .switch-icons 
    @extend %position-absolute 
    top: -2px 
    left: 0
    @extend %w-100
    @extend %h-100 
    cursor: pointer
    +media(1199px) 
      left: -8px 
    svg
      @extend %position-absolute 
      top: 50%
      left: 50%
      +transform(translate(-50%, -50%))
      &.feather-sun
        opacity: 1
      &.feather-moon 
        opacity: 0
