/* dropdown css start */
.dropdown-menu
  padding: rem(10px) 0
  border: 1px solid var(--border-color)
  box-shadow: 0 5px 10px rgba(#000, 0.05)
  background-color: var(--card-bg)
  li 
    font-size: 14px 
    .dropdown-item 
      color: var(--para-color)
      &:active, &:focus, &:hover 
        background-color: var(--body-bg)
        @extend %text-base 
      svg 
        width: rem(14px)
        height: rem(14px)
        margin-inline-end: rem(6px)
/* dropdown css end */