h1, h2, h3, h4, h5, h6 
  color: var(--h-color) 
    
h1, .h1 
  font-size: var(--h1)
  line-height: var(--h1-lineHeight)
  +media(1399px)
    font-size: var(--h1-lg)

h2, .h2 
  font-size: var(--h2)
  line-height: var(--h2-lineHeight)
  +media(1399px)
    font-size: var(--h2-lg)

h3, .h3 
  font-size: var(--h3)
  line-height: var(--h3-lineHeight)
  +media(1399px)
    font-size: var(--h3-lg)

h4, .h4 
  font-size: var(--h4)
  line-height: var(--h4-lineHeight)
  +media(1399px)
    font-size: var(--h4-lg)


h5, .h5 
  font-size: var(--h5)
  line-height: var(--h5-lineHeight)
  +media(1399px)
    font-size: var(--h5-lg) 

h6, .h6 
  font-size: var(--h6)
  line-height: var(--h6-lineHeight)

.line-height-1 
  line-height: 1

.text-small
  font-size: rem(14px) !important /* 14px */

.text-xs
  font-size: rem(12px) !important /* 12px */

.fw-300
  font-weight: var(--f-light) !important

.fw-400
  font-weight: var(--f-regular) !important

.fw-500
  font-weight: var(--f-medium) !important

.fw-600
  font-weight: var(--f-semibold) !important

.fw-700
  font-weight: var(--f-bold) !important

.text-line-1
  display: -webkit-box
  -webkit-line-clamp: 1
  -webkit-box-orient: vertical
  overflow: hidden
  text-overflow: ellipsis

.text-line-2
  display: -webkit-box
  -webkit-line-clamp: 2
  -webkit-box-orient: vertical
  overflow: hidden
  text-overflow: ellipsis

.text-line-3
  display: -webkit-box
  -webkit-line-clamp: 3
  -webkit-box-orient: vertical
  overflow: hidden
  text-overflow: ellipsis

.white-space-initial 
  white-space: initial !important

.white-space-nowrap 
  white-space: nowrap !important
