/* widget css start */
.s7__widget
  @extend %card-bg
  padding: rem(20px)
  @extend %bs-8
  @extend %shadow-1
  @extend %transition 
  &:hover
    @extend %shadow-2
    @extend %trans-y
  .top
    @extend %d-flex
    @extend %align-center
    i
      margin-inline-end: rem(10px)
      font-size: rem(24px)
    .caption
      margin-bottom: 0
  .amount
    margin-top: rem(15px)
    margin-bottom: 0
    word-break: break-all
    +media(1550px)
      font-size: rem(22px)

.s7__widget-two
  @extend %d-flex
  @extend %card-bg
  padding: rem(20px)
  @extend %shadow-1
  @extend %bs-8
  @extend %transition 
  &:hover
    @extend %shadow-2
    @extend %trans-y
  .icon
    width: 60px
    border-right: 1px solid var(--border-color)
    @extend %d-flex
    @extend %align-center
    @extend %justify-center
    i
      font-size: rem(32px)
  .content
    width: calc(100% - 60px)
    padding-inline-start: rem(20px)
    .amount
      word-break: break-all

.s7__widget-three
  padding: rem(20px)
  @extend %card-bg
  @extend %bs-8
  @extend %shadow-1
  @extend %d-flex
  @extend %align-center
  @extend %transition 
  &:hover
    @extend %shadow-2
    @extend %trans-y
  .content
    width: calc(100% - 55px)
    padding-inline-end: 15px
  .icon
    width: 55px
    height: 55px
    @extend %d-flex
    @extend %align-center
    @extend %justify-center
    @extend %position-relative
    @extend %overflow-hidden
    @extend %z-index-p
    &::before
      @extend %position-absolute
      content: ''
      top: 0
      left: 30%
      width: 8px
      height: 65px
      @extend %bg-white
      opacity: 0.075
      @extend %z-index-c
      +transform(rotate(20deg))
    &::after
      @extend %position-absolute
      content: ''
      top: 0
      right: 30%
      width: 8px
      height: 65px
      @extend %bg-white
      opacity: 0.075
      @extend %z-index-c
      +transform(rotate(20deg))
    i
      @extend %text-white
      font-size: rem(24px)

.s7__widget-four
  background-color: var(--card-bg)
  padding: rem(20px)
  @extend %bs-8
  @extend %shadow-1
  @extend %d-flex
  @extend %transition 
  &:hover
    @extend %shadow-2
    @extend %trans-y
  .icon
    width: 55px
    height: 55px
    @extend %d-flex
    @extend %justify-center
    @extend %align-center
    i
      width: 45px
      height: 45px
      @extend %d-flex
      @extend %justify-center
      @extend %align-center
      @extend %bs-50
      font-size: rem(20px)
  .content
    width: calc(100% - (55px + 80px))
    padding: 0 rem(16px)
    .caption
      margin-bottom: rem(5px)
    .amount
      margin-bottom: 0
  .up-down-ratio
    font-size: rem(12px)
    font-weight: 500
    width: 80px
    @extend %d-flex
    justify-content: flex-end
    align-items: flex-end
    >div
      @extend %d-inline-flex
      @extend %align-center
      padding: rem(1px) rem(5px)
      @extend %bs-5
      @extend %text-center
      i
        margin-inline-end: rem(3px)
    .up-ratio
      background-color: rgba($success, 0.1)
      color: $success
    .down-ratio
      background-color: rgba($danger, 0.1)
      color: $danger

.s7__icon
  @extend %position-relative
  @extend %z-index-p
  i
    @extend %base-color
    @extend %text-white
  &::before
    @extend %positionTwo
    @extend %base-color
    opacity: 0.15
    @extend %bs-50
    @extend %z-index-c
  &.s7__icon-1
    i
      background-color: #764AF1
    &::before
      background-color: #764AF1
  &.s7__icon-2
    i
      background-color: #2155CD
    &::before
      background-color: #2155CD
  &.s7__icon-3
    i
      background-color: #005555
    &::before
      background-color: #005555
  &.s7__icon-4
    i
      background-color: #4D4C7D
    &::before
      background-color: #4D4C7D
  &.s7__icon-5
    i
      background-color: #143F6B
    &::before
      background-color: #143F6B
  &.s7__icon-6
    i
      background-color: #FD5D5D
    &::before
      background-color: #FD5D5D
  &.s7__icon-7
    i
      background-color: #019267
    &::before
      background-color: #019267
  &.s7__icon-8
    i
      background-color: #5463FF
    &::before
      background-color: #5463FF
  &.s7__icon-9
    i
      background-color: #FF5C8D
    &::before
      background-color: #FF5C8D
  &.s7__icon-10
    i
      background-color: #F900BF
    &::before
      background-color: #F900BF

.s7__widget-five
  padding: rem(37px)
  @extend %shadow-1
  @extend %bs-8
  background-color: var(--card-bg)
  @extend %text-center
  @extend %transition 
  +media(1399px)
    padding: rem(20px) 
  &:hover
    @extend %shadow-2
    @extend %trans-y
  .icon
    font-size: rem(36px)
    margin-bottom: rem(20px)
    line-height: 1
  .progress
    height: 6px
    background-color: var(--body-bg)

.theme-dark
  [class*="s7__widget"]
    border: 1px solid var(--border-color)
    box-shadow: 0 5px 25px rgba(#000, 0.2)
/* widget css end */