.progessbar-wrapper 
  display: flex 
  margin: 0 rem(-3px) 
  .s7__progressbar
    margin: rem(0) rem(3px)

.s7__progressbar
  @extend %position-relative 
  background-color: var(--body-bg)
  height: 8px 
  +border-radius(999px) 
  .s7__progressbar-inner 
    @extend %position-absolute 
    top: 0 
    left: 0 
    height: 100% 
    +border-radius(999px) 


.circle-progress-bar 
  position: relative 
  height: 100px 
  width: 100px 
  div 
    position: absolute 
    height: 100px 
    width: 100px 
    border-radius: 50% 
    span 
      position: absolute 
      font-family: Arial 
      font-size: 25px 
      line-height: 87.5px 
      height: 87.5px 
      width: 87.5px 
      left: 6.25px 
      top: 6.25px 
      text-align: center 
      border-radius: 50% 
      background-color: var(--card-bg) 
  .background 
    background-color: var(--body-bg) !important
  .rotate 
    clip: rect(0 50px 100px 0) 
    background-color: #4b86db  
  .left 
    clip: rect(0 50px 100px 0) 
    opacity: 1 
    background-color: #b3cef6 
  .right 
    clip: rect(0 50px 100px 0) 
    transform: rotate(180deg) 
    opacity: 0 
    background-color: #4b86db 
@keyframes toggle 
  0% 
    opacity: 0 
  100% 
    opacity: 1 
