/* support ticket css start */
.support-ticket-nav-area
  @extend %position-relative
  .grid-btn-area
    @extend %position-absolute
    top: 50%
    +transform(translateY(-50%))
    right: 0
    margin: 0 rem(-2px)
    +media(767px)
      display: none
    .view-style-btn
      padding: 0
      border: none
      width: 30px
      height: 30px
      background-color: rgba(var(--color-primary), 0.15)
      @extend %bs-5
      margin: 0 rem(2px)
      @extend %transition
      &.active
        @extend %base-color
        svg
          stroke: #fff
    svg
      width: rem(18px)
      height: rem(18px)
      fill: rgba(var(--color-primary), 0.25)
      stroke: var(--color-primary)
      @extend %transition

.s7__ticket-card
  padding: rem(25px)
  background-color: var(--card-bg)
  border: 1px solid var(--border-color) 
  @extend %bs-5
  @extend %position-relative
  @extend %transition
  @extend %overflow-hidden
  @extend %z-index-p
  &::before
    @extend %position-absolute
    content: ''
    top: 0
    left: 0
    width: 3px
    @extend %h-100
    @extend %base-color
    opacity: 0
  &::after
    @extend %positionTwo
    background: #eea849
    background: -webkit-linear-gradient(to top, #eea849, #fff)
    background: linear-gradient(to top, #eea849, #fff)
    @extend %z-index-c
    opacity: 0

  &:hover
    box-shadow: 0 10px 25px rgba(#000, 0.05)
  & + &
    margin-top: rem(30px)
  &.new-ticket
    &::before
      opacity: 1
    &::after
      opacity: 0.1
  .ticket-link
    @extend %position-absolute
    top: 0
    left: 0
    @extend %w-100
    @extend %h-100
    @extend %z-index-p
  &-header
    @extend %d-flex
    @extend %align-center
    justify-content: space-between
    margin-bottom: rem(25px)
    .left
      @extend %d-flex
      @extend %align-center
      i
        width: 30px
        height: 30px
        background-color: rgba(var(--color-primary), 0.15)
        @extend %text-base
        @extend %d-flex
        @extend %align-center
        @extend %justify-center
        @extend %bs-50
        margin-inline-end: rem(6px)
  &-footer
    @extend %d-flex
    @extend %align-center
    justify-content: space-between
    margin-top: rem(20px)
    .ticket-creator
      @extend %d-flex
      @extend %align-center
      h6
        font-size: rem(15px)
      .thumb
        width: 40px
        height: 40px
        margin-inline-end: rem(10px)
        img
          @extend %w-100
          @extend %h-100
          @extend %obj-fit
    .ticket-meta-list
      padding: 0
      margin: rem(-4px) rem(-10px)
      list-style: none
      @extend %d-flex
      @extend %align-center
      li
        padding: rem(4px) rem(10px)

.support-ticket-wrapper
  &.grid-view-wrapper
    display: grid
    grid-template-columns: repeat(3, 1fr)
    grid-gap: 15px
    +media(1399px)
      grid-template-columns: repeat(2, 1fr)
    +media(767px)
      grid-template-columns: repeat(1, 1fr)
    .s7__ticket-card + .s7__ticket-card
      margin-top: 0
/* support ticket css end */

/* support ticket details css start */
.ticket-details
  background-color: var(--card-bg)
  border: 1px solid var(--border-color)
  @extend %bs-8
  height: calc(100vh - 105px)
  display: flex
  flex-direction: column
  &-header
    @extend %d-flex
    justify-content: space-between
    padding: rem(15px) rem(20px)
    @extend %align-center
    border-bottom: 1px solid var(--border-color)
    .left
      @extend %d-flex
      .icon
        width: 50px
        height: 50px
        background-color: rgba(#ddd, 0.35)
        @extend %d-flex
        @extend %align-center
        @extend %justify-center
        @extend %bs-50
        font-size: rem(28px)
      .content
        width: calc(100% - 50px)
        padding-inline-start: rem(20px)
        ul
          padding: 0
          margin: 0
          @extend %d-flex
          @extend %align-center
          list-style: none
          margin-inline-start: rem(-10px)
          li
            padding: rem(3px) rem(10px)
            font-size: rem(14px)
            font-weight: 500
    .right
      margin-top: rem(3px)
  &-body
    padding: rem(20px)
    flex-grow: 1
    flex-shrink: 1
    flex-basis: auto
    overflow: auto
    scrollbar-width: thin
    scrollbar-color: rgba(#000, 0.25) var(--card-bg)
    &::-webkit-scrollbar
      width: 12px
    &::-webkit-scrollbar-track
      background: var(--card-bg)
    &::-webkit-scrollbar-thumb
      background-color: rgba(#000, 0.25)
      border-radius: 20px
      border: 3px solid var(--card-bg)
  &-footer
    padding: rem(20px)
  .support-form
    @extend %position-relative
    &-footer
      @extend %position-absolute
      bottom: 15px
      left: 15px
      @extend %d-flex
      align-items: flex-end
      width: calc(100% - 30px)

.single-message
  @extend %d-flex
  padding: rem(25px) 0
  border-bottom: 1px solid var(--border-color)
  &:first-child
    padding-top: 0
  &:last-child
    padding-bottom: 0
    border-bottom: none
  .thumb
    width: 50px
    height: 50px
    @extend %overflow-hidden
    @extend %bs-50
    img
      @extend %w-100
      @extend %h-100
      @extend %obj-fit
  .content
    width: calc(100% - 70px)
    padding-inline-start: rem(20px)
  .attached-files
    @extend %d-flex
    margin: rem(10px) rem(-5px) rem(-5px) rem(-5px)
    a
      display: block
      width: 100px
      height: 100px
      margin: rem(5px)
      @extend %overflow-hidden
      @extend %bs-5
      border: 4px solid var(--body-bg)
      box-shadow: 0 3px 15px rgba(#000, 0.15)
      img
        @extend %w-100
        @extend %h-100
        @extend %obj-fit

.file
  @extend %position-relative
  width: calc(100% - 90px)
  padding-inline-end: rem(20px)
  @extend %d-flex
  @extend %align-center
  &__input
    border-radius: 3px
    color: var(--para-color)
    width: 30px
    cursor: pointer
    &--file
      position: absolute
      opacity: 0
      width: 30px
      cursor: pointer
    &--label
      @extend %d-inline-flex
      justify-content: space-between
      align-items: center
      margin-bottom: 0
      cursor: pointer
      width: 30px
      font-size: rem(22px)
  &__value
    @extend %position-relative
    padding: rem(3px) rem(40px) rem(3px) rem(10px)
    display: flex
    @extend %align-center
    color: var(--para-color)
    background-color: var(--body-bg)
    cursor: pointer
    margin-inline-start: rem(15px)
    @extend %bs-5
    @extend %overflow-hidden
    &:hover
      &:after
        color: rgba(0, 0, 0, 1)
    &:after
      @extend %position-absolute
      content: "\f00d"
      font-family: 'Line Awesome Free'
      font-weight: 900
      cursor: pointer
      margin-inline-start: 5px
      font-size: 15px
      width: 25px
      height: 100%
      right: 0
      top: 0
      display: flex
      justify-content: center
      align-items: center
      background-color: $danger
      color: #fff

      &:hover
        color: rgba(0, 0, 0, 1)
  &__remove
    display: block
    width: 20px
    height: 20px
    border: 1px solid #000

.theme-dark
  .ticket-details-body
    scrollbar-color: rgba(#fff, 0.25) var(--card-bg)
    &::-webkit-scrollbar-thumb
      background-color: rgba(#fff, 0.25)
/* support ticket details css end */
