/* s7__main css start */
.s7__main
  padding: 90px 25px 25px 275px
  @extend %transition 
  +media(1199px)
    padding: 85px 20px 20px 20px
    +media(575px)
      padding-left: rem(15px)
      padding-right: rem(15px)
  &.collaped 
    padding-inline-start: 100px

.items-wrapper
  @extend %d-flex
  background-color: var(--card-bg)
  border-bottom: 1px solid var(--border-color)
  .single-item
    width: calc(100% / 4)
    padding: rem(15px)
    border-right: 1px solid var(--border-color)
    &:last-child
      border-right: none

.s7__page-nav 
  margin-bottom: rem(25px)
  @extend %d-flex 
  justify-content: space-between 
  @extend %align-center 
  .title 
    margin-bottom: 0 
  ul 
    list-style: none 
    @extend %d-flex 
    padding-inline-start: 0
    margin-bottom: 0
    li 
      &:last-child
        &::after 
          display: none
      &::after 
        content: '/' 
        padding: 0 rem(10px)
      a 
        @extend %text-base 

.single-region
  .progress
    height: 8px
    background-color: var(--body-bg)

.theme-dark
  #overviewChart
    .apexcharts-xaxis
      text
        fill: var(--h-color)
  .apexcharts-legend-text 
    color: #fff !important 
  .apexcharts-tooltip.apexcharts-theme-light
    background: var(--body-bg) 
    border: 1px solid var(--border-color) 
    .apexcharts-tooltip-title 
      background: var(--body-bg)
      border-bottom: 1px solid var(--border-color) 
/* s7__main css end */