[dir="rtl"]
  .s7__nav-search-form 
    button
      right: auto 
      left: 4px 
.s7__nav 
  position: fixed 
  top: 0 
  left: 0 
  width: 100% 
  padding: 0 20px 0 300px 
  border-bottom: 1px solid var(--border-color)
  @extend %d-flex 
  justify-content: space-between
  @extend %align-center
  @extend %card-bg 
  z-index: 2
  @extend %transition
  +media(1199px)
    padding: 0 20px
  &.collaped 
    padding-inline-start: 140px  
    .sidebar-collapse-btn
      left: 95px
      &::before  
        top: 4px
        width: 60% 
        +transform(rotate(-35deg))
      &::after 
        bottom: 4px
        width: 60% 
        +transform(rotate(35deg))
      .line 
        opacity: 0 
  &.nav-text-white 
    .s7__nav-search-form 
      background-color: rgba(#fff, 0.1)
      input 
        color: #fff
        +placeholder(#ffffff85)
    .sidebar-collapse-btn  
      color: #fff 
    .s7__nav-right 
      >li 
        &.user-menu-dropdown 
          &::after 
            color: #fff 
          button 
            background-color: rgba(#fff, 0.1)
            span 
              color: #fff 
      >li
        button, span
          svg 
            stroke: #fff 
        
  .sidebar-collapse-btn 
    @extend %position-absolute 
    top: 50%
    left: 260px
    font-size: rem(22px)
    background-color: transparent 
    width: 22px 
    height: 18px 
    +transform(translateY(-50%))
    border: none 
    @extend %transition
    color: var(--para-color)
    .line 
      @extend %position-absolute  
      top: 50% 
      left: 0 
      margin-top: -1px 
      width: 15px 
      height: 2px
      background-color: var(--color-primary) 
      @extend %transition 
    &::before 
      @extend %position-absolute 
      content: ''
      top: 0 
      left: 0 
      width: 100%
      height: 2px
      background-color: var(--color-primary)
      @extend %transition 
    &::after 
      @extend %position-absolute 
      content: ''
      bottom: 0 
      left: 0 
      width: 100%
      height: 2px
      background-color: var(--color-primary)
      @extend %transition 
    +media(1199px)
      display: none 
  .s7__nav-search-form
    &.active 
      top: 66px 
      opacity: 1
      visibility: visible
    +media(1199px) 
      position: fixed 
      top: 100px 
      left: 0
      width: 100%
      background-color: #fff 
      opacity: 0 
      visibility: hidden
      box-shadow: 0 5px 10px rgba(#000, 0.15)

.notification-dropdown 
  width: 400px
  +media(575px)
    width: 300px
  .notification-dropdown-wrapper 
    max-height: 275px 
    overflow-y: auto 
    scrollbar-width: thin
    scrollbar-color: #fff
    &::-webkit-scrollbar 
      width: 12px
    &::-webkit-scrollbar-track 
      background: #fff
    &::-webkit-scrollbar-thumb 
      background-color: #ddd
      border-radius: 20px
      border: 3px solid #fff
  .notification-dropdown-header 
    display: flex 
    flex-wrap: wrap
    justify-content: space-between
    align-items: center
    padding: rem(12px) rem(16px)
    border-bottom: 1px solid var(--border-color)
  .dropdown-item 
    padding: rem(10px) rem(16px)
    border-bottom: 1px solid var(--border-color)
    color: var(--para-color) 
  .notification-icon 
    width: 40px 
    height: 40px
    @extend %d-flex 
    @extend %justify-center
    @extend %align-center 
  .notification-content 
    width: calc(100% - 40px)
    padding-inline-start: rem(15px) 


.nav-search-btn 
  display: none 
  width: 20px 
  height: 20px 
  @extend %position-relative 
  &.active 
    .feather-x 
      opacity: 1
    .feather-search 
      opacity: 0
  +media(1199px) 
    display: inline-block
  .feather-x ,
  .feather-search
    @extend %position-absolute 
    top: 50% 
    left: 50% 
    +transform(translate(-50%, -50%)) 
    @extend %transition 
  .feather-x
    opacity: 0 
  
.s7__nav-search-form
  @extend %position-relative 
  width: 250px
  background-color: var(--body-bg)
  padding: rem(4px)
  @extend %bs-5
  @extend %transition 
  input 
    padding: 10px 
    background-color: transparent 
    height: 37px
    border: none
    @extend %w-100 
    color: var(--h-color) 
    &:focus, &:focus-visible
      outline: none 
  button 
    @extend %position-absolute
    top: 4px
    right: 4px
    height: calc(100% - 8px)
    width: 40px
    border: none 
    @extend %d-flex 
    @extend %align-center
    @extend %justify-center
    @extend %base-color
    @extend %bs-5 
    box-shadow: 0 3px 5px rgba(var(--color-primary), 0.3)
    svg 
      width: 16px
      height: 16px
      stroke: #fff 

.s7__nav-right 
  @extend %list-reset
  @extend %d-flex 
  @extend %align-center 
  li 
    &.user-menu-dropdown 
      @extend %position-relative 
      &::after 
        @extend %position-absolute 
        content: "\f107"
        top: 50% 
        right: 15px 
        font-family: 'Line Awesome Free'
        font-weight: 900
        font-size: rem(12px)
        +transform(translateY(-50%))
      button 
        background-color: #f3f3f9
        padding: rem(20px) rem(35px) rem(20px) rem(20px)
        span 
          font-size: rem(14px)
    button 
      padding: rem(20px) rem(15px)
      border: none 
      background-color: transparent
      @extend %d-flex 
      @extend %align-center
    svg 
      width: rem(18px)
      height: rem(18px)
      fill: rgba(202, 203, 204, 0.5)
      stroke: #545a6d 

.has-notification 
  @extend %position-relative 
  &::before 
    @extend %position-absolute 
    content: '' 
    top: 13px
    right: 10px
    width: 7px 
    height: 7px 
    background-color: var(--color-primary) 
    @extend %bs-50 
  &::after 
    @extend %position-absolute 
    content: '' 
    top: 13px
    right: 10px
    width: 7px 
    height: 7px 
    background-color: var(--color-primary) 
    @extend %bs-50 
    animation: ping 1s cubic-bezier(0,0,.2,1) infinite

@keyframes ping
  75%,to
    opacity: 0
    transform: scale(2)

.offcanvas 
  background-color: var(--card-bg)

.theme-dark 
  .s7__sidebar-nav 
    li 
      &:hover 
        a 
          svg 
            stroke: var(--color-primary) 
      a 
        svg
          fill: rgba(202, 203, 204, 0.212)
          stroke: #a5b9d5
  .s7__nav-right 
    li 
      &.user-menu-dropdown 
        button 
          background-color: var(--body-bg)
          color: var(--h-color)
      svg  
        fill: rgba(202, 203, 204, 0.212)
        stroke: #a5b9d5
  .notification-dropdown-wrapper 
    scrollbar-color:  var(--card-bg)
    &::-webkit-scrollbar-track 
      background: var(--card-bg)
    &::-webkit-scrollbar-thumb 
      background-color: var(--body-bg)
      border-radius: 20px
      border: 3px solid var(--card-bg)

        