/* ==== sidebar css start ==== */
.s7__sidebar
  position: fixed 
  top: 0 
  left: 0 
  padding: 0 
  list-style: none 
  width: 250px 
  min-height: 100vh 
  z-index: 3
  @extend %transition 
  border-right: 1px solid var(--border-color)
  background-color: var(--card-bg)
  &.active 
    left: 0 
  +media(1199px) 
    left: -280px
  &.sidebar-text-white 
    border-right-color: rgba(#fff, 0.1)
    .s7__menu-title 
      color: #ddd
    .s7__sidebar-nav
      li 
        a  
          @extend %text-white 
          svg 
            stroke: #fff 
  .short-logo-icon 
    display: none
  &.collaped:not(:hover)
    width: 85px 
    .s7__logo
      .long-logo 
        display: none
      .short-logo-icon 
        display: block 
    .s7__sidebar-nav
      > li 
        &.has-child::after
          display: none
        a 
          padding: 0 
          height: 38px
          display: flex 
          justify-content: center
          align-items: center
          svg 
            margin-inline-end: 0 
        .s7__nav-caption
          // display: none
          width: 0 
          opacity: 0
      .s7__menu-title 
        // display: none 
        height: 0 
        opacity: 0


  .sidebar-close-btn 
    display: none 
    background-color: transparent 
    @extend %text-white 
    font-size: rem(22px)
    @extend %position-absolute 
    top: 0 
    right: 5px
    border: 0 
    padding: 0 
    +media(1199px) 
      display: block
.s7__logo 
  @extend %d-block 
  margin-bottom: rem(20px)
  padding: rem(20px)
  @extend %transition 
  img 
    max-width: rem(200px)
    max-height: rem(38px)

.sidebar-open-btn 
  border: none 
  background-color: transparent
  @extend %d-flex 
  @extend %align-center
  display: none
  +media(1199px)
    display: block
  svg 
    width: rem(24px)
    height: rem(24px)
    fill: rgba(202, 203, 204, 0.5)
    stroke: #545a6d 

.s7__sidebar-nav-wrapper 
  overflow: auto 
  height: calc(100vh - 100px)
  padding: 0 rem(20px)
.simplebar-scrollbar
  &.simplebar-visible 
    &::before 
      opacity: 0.12
  &::before
    background: #000 
    opacity: 0.1

.theme-dark 
  .simplebar-scrollbar
    &::before
      background: #fff 

.s7__sidebar-nav 
  @extend %list-reset
  margin-inline-start: -5px 
  > li + li 
    margin-top: rem(3px)
  >li 
    &.active 
      a  
        @extend %base-color 
        @extend %text-white 
        svg 
          stroke: #fff 
    &.open  
      &::after 
        color: #fff !important 
      > a  
        @extend %base-color 
        @extend %text-white 
        svg 
          stroke: #fff 
  li 
    &.has-child 
      @extend %position-relative 
      &.open 
        .s7__sub-nav
          display: block !important
      &::after 
        @extend %position-absolute
        top: rem(10px)
        right: rem(10px)
        content: "\f105" 
        font-family: 'Line Awesome Free'
        font-weight: 900
        font-size: rem(14px)
        @extend %transition
        color: #6a7c95
    a
      @extend %d-flex 
      @extend %align-center 
      font-size: rem(14px)
      color: #6a7c95
      @extend %text-d-none
      padding: rem(8px) rem(15px) rem(8px) rem(10px)
      font-weight: 500 
      @extend %transition
      @extend %bs-5 
      &:hover 
        @extend %text-base 
        svg 
          stroke: var(--color-primary)
      svg 
        width: rem(18px)
        height: rem(18px)
        fill: rgba(202, 203, 204, 0.322)
        stroke: #6a7c95
        margin-inline-end: rem(12px)
        @extend %transition
  
  .s7__sub-nav
    @extend %list-reset
    padding-inline-start: rem(24px)
    margin-inline-start: rem(22px)
    border-left: 1px solid var(--border-color) 
    li 
      a 
        padding: rem(5px) 0
        font-size: rem(14px)
        &.active 
          color: var(--color-primary)

.s7__menu-title 
  text-transform: uppercase 
  font-weight: 500
  font-size: rem(12px)
  margin-top: rem(25px) !important
  margin-bottom: rem(10px)
  color: darken(#a5b9d5, 10%)
  @extend %transition 

.theme-dark 
  .s7__sidebar 
    border-right: 1px solid var(--border-color)
/* ==== sidebar css end ==== */