.media-40px
  width: 40px
  height: 40px

.media-50px
  width: 50px
  height: 50px

.media-60px
  width: 60px
  height: 60px

.media-70px
  width: 70px
  height: 70px

.media-80px
  width: 80px
  height: 80px

.media-90px
  width: 90px
  height: 90px

.media-100px
  width: 100px
  height: 100px

.fit-cover 
  @extend %w-100
  @extend %h-100
  @extend %obj-fit

.preloader 
  position: fixed
  top: 0 
  left: 0
  @extend %w-100 
  @extend %h-100 
  background-color: rgba(#fff, 0.15)
  backdrop-filter: blur(10px)
  z-index: 9
  @extend %d-flex 
  @extend %justify-center
  @extend %align-center
  .preloader-icon-img 
    animation: loader 1s infinite linear
    img 
      max-width: 45px

+keyframes(loader)
  from 
    -webkit-transform: rotate(0deg)
    transform: rotate(0deg)
  to
    -webkit-transform: rotate(359deg)
    transform: rotate(359deg)
/* global css end */ 